}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
:root {
  font-size: 16px;
  font-family: "Montserrat", sans-serif;
  --text-primary: #b6b6b6;
  --text-secondary: #ececec;
  --bg-primary: #23232e;
  --bg-secondary: #141418;
}

body::-webkit-scrollbar {
  width: 0.2rem;
  background: red;
}
body::-webkit-scrollbar-track {
  background: black;
}
body::-webkit-scrollbar-thumb {
  background: #23232e;
  background: var(--bg-primary);
  border-radius: 30px;
  height: 10px;
}
main {
  padding-top: 1rem;
  margin-left: 5rem;
}
h1 {
  font-size: 30px;
  padding-bottom: 10px;
}
p {
  padding: 5px;
